'use client'

import { useState, useEffect } from 'react'
import Image from 'next/image'
import { ChevronLeft, ChevronRight, Play, Plus } from 'lucide-react'
import { Anime } from '@/lib/api'

interface HeroCarouselProps {
  animes: Anime[]
  height?: string
  autoplayInterval?: number
}

export function HeroCarousel({
  animes,
  height = 'h-[28rem] md:h-[44rem]',
  autoplayInterval = 5000,
}: HeroCarouselProps) {
  const [current, setCurrent] = useState(0)
  const total = animes.length

  const nextSlide = () => setCurrent((current + 1) % total)
  const prevSlide = () => setCurrent((current - 1 + total) % total)

  // Autoplay
  useEffect(() => {
    if (total <= 1) return
    const timer = setInterval(() => nextSlide(), autoplayInterval)
    return () => clearInterval(timer)
  }, [current, total])

  if (total === 0) return null

  return (
    <div className={`relative w-full overflow-hidden rounded-xl ${height}`}>
      {animes.map((anime, idx) => {
        const isActive = idx === current
        return (
          <div
            key={anime.mal_id}
            className={`absolute inset-0 transition-all duration-1000 ease-in-out transform ${
              isActive ? 'opacity-100 scale-105 z-10' : 'opacity-0 scale-95 z-0'
            }`}
          >
            {/* Gambar jernih */}
            <Image
              src={anime.images?.jpg.original_image_url || anime.images?.jpg.large_image_url || '/placeholder.jpg'}
              alt={anime.title}
              fill
              className="object-cover"
              priority={isActive}
            />

            {/* Overlay */}
            <div className="absolute inset-0 bg-gradient-to-t from-black/70 via-black/20 to-transparent"></div>

            {/* Konten */}
            <div className="absolute bottom-8 left-8 md:bottom-12 md:left-12 max-w-lg text-white">
              <h2 className="text-3xl md:text-5xl font-bold mb-3">{anime.title}</h2>
              <p className="text-base md:text-lg mb-4 line-clamp-3">{anime.synopsis || 'No description.'}</p>
              <div className="flex gap-4">
                <button className="flex items-center gap-2 bg-primary px-5 py-3 rounded hover:bg-primary/80 transition-colors">
                  <Play size={18} /> Watch
                </button>
                <button className="flex items-center gap-2 bg-white/20 px-5 py-3 rounded hover:bg-white/30 transition-colors">
                  <Plus size={18} /> Add to List
                </button>
              </div>
            </div>
          </div>
        )
      })}

      {/* Navigation */}
      {total > 1 && (
        <>
          <button onClick={prevSlide} className="absolute left-4 top-1/2 -translate-y-1/2 bg-black/30 hover:bg-black/50 text-white p-4 rounded-full z-20">
            <ChevronLeft size={32} />
          </button>
          <button onClick={nextSlide} className="absolute right-4 top-1/2 -translate-y-1/2 bg-black/30 hover:bg-black/50 text-white p-4 rounded-full z-20">
            <ChevronRight size={32} />
          </button>
        </>
      )}

      {/* Dots */}
      <div className="absolute bottom-4 left-1/2 -translate-x-1/2 flex gap-2 z-20">
        {animes.map((_, idx) => (
          <span key={idx} className={`w-3 h-3 rounded-full transition-colors ${idx === current ? 'bg-white' : 'bg-white/50'}`} />
        ))}
      </div>
    </div>
  )
}
